.Items {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 125px;
  height: 200px;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 20px 18px;
  border-radius: 10px;
  text-align: left;
  cursor: pointer;
}

.Items a {
  text-decoration: none;
  color: #fff;
}

.Items-go {
  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 15px;
  bottom: 19px;
  color: #fff;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  background-color: rgb(43, 189, 43);
}

.Items:hover .Items-go {
  cursor: pointer;
  display: flex;
}

.Items img {
  width: 120px;
  height: 120px;
  object-fit: cover;
}

.Items h5 {
  margin-top: 10px;
  font-size: 15px;
}

.Items-p1 {
  width: 100%;
  text-align: left;
  margin-top: 10px;
  margin-bottom: 8px;
  font-size: 17px;
  font-weight: 500;
  padding-top: 4px;
}

.Items-p2 {

  width: 100%;
  font-size: 10px;
  opacity: 0.7;
}
