@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  outline: none;
}

.App {
  display: flex;
}
.Navbar {
  position: fixed;
  width: 17%;
  height: 100vh;
  background-color: rgb(0, 0, 0);
  color: #fff;
}

.Navbar-logo {
  margin-top: -34px;
  padding-left: 21px;
}

.Navbar-logo img {
  width: 134px;
}

.Navbar-list {
  margin-top: -15px;
}

.Navbar-list a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
}

.Navbar-list a.active li {
  color: #fff;
}

.Navbar-list ul li {
  padding: 5px 0px;
  display: flex;
  align-items: center;
  margin: auto;
  width: 95%;
  border-radius: 5px;
}

.Navbar-span-icon {
  padding-left: 18px;
  font-size: 28px;
}

.Navbar-span-text {
  padding-left: 15px;
  padding-top: -1px;
}

.Navbar-playlist {
  padding: 27px 25px;
}

.Navbar-playlist div {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  cursor: pointer;
}

.Navbar-playlist div:hover span {
  color: #fff;
}

.Navbar-playlist span {
  font-size: 14px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.7);
  margin-top: -4px;
  padding-left: 17px;
}

.Navbar-playlist h5 {
  opacity: .7;
  font-weight: 500;
  letter-spacing: 1px;
  margin-bottom: 17px;
  cursor: default;
}

.add-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  width: 32px;
  height: 32px;
  background-color: rgba(255, 255, 255, 0.7);
}

.favorite-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  width: 32px;
  height: 32px;
  background: linear-gradient(120deg, rgb(89, 89, 197), rgb(87, 72, 173));
  color: rgba(255, 255, 255, 0.7);
  font-size: 16px;
}

.navbar-favorite-list ul {
  padding-top: 10px;
  list-style: none;
  font-size: 15px;
  line-height: 30px;
  opacity: 0.8;
}
.MainPage {
  width: 83%;
  height: 1500px;
  margin-left: 17%;
  text-align: center;
  color: #fff;
  background-color: rgba(0, 0, 0, .9);
}

.wrapper {
  text-align: left;
  padding: 30px 35px;
}

.wrapper small {
  opacity: 0.6;
}

.wrapper-items {
  margin-top: 10px;
  width: 100;
  height: 250px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Items {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 125px;
  height: 200px;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 20px 18px;
  border-radius: 10px;
  text-align: left;
  cursor: pointer;
}

.Items a {
  text-decoration: none;
  color: #fff;
}

.Items-go {
  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 15px;
  bottom: 19px;
  color: #fff;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  background-color: rgb(43, 189, 43);
}

.Items:hover .Items-go {
  cursor: pointer;
  display: flex;
}

.Items img {
  width: 120px;
  height: 120px;
  object-fit: cover;
}

.Items h5 {
  margin-top: 10px;
  font-size: 15px;
}

.Items-p1 {
  width: 100%;
  text-align: left;
  margin-top: 10px;
  margin-bottom: 8px;
  font-size: 17px;
  font-weight: 500;
  padding-top: 4px;
}

.Items-p2 {

  width: 100%;
  font-size: 10px;
  opacity: 0.7;
}

.CollectionPage {
  width: 83%;
  height: 1500px;
  margin-left: 17%;
  color: #fff;
  background-color: rgba(0, 0, 0, .9);
}

.CollectionPageItems {
  padding: 30px 35px;
}

.CollectionPageItems-small {
  margin-bottom: 15px;
  opacity: 0.6;
}

.Collection-items {
  display: flex;
  margin-left: -17px;
}

.Collection-wrap {
  margin-left: 15px;
}
.mypage-list {
  display: flex;
  align-items: center;
  margin-top: 10px;
  width: 90%;
  background-color: rgba(255, 255 ,255, 0.1);
  padding: 1rem;
  font-size: 13px;
  cursor: pointer;
}

.mypage-id {
  width: 2%;
}

.mypage-name {
  width: 55%;
}

.mypage-album {
  width: 39%;
}
.MyPlayListPage {
  width: 83%;
  height: 1500px;
  margin-left: 17%;
  color: #fff;
  background-color: rgb(8, 8, 8);
}

.MyPlayListPage-header {
  height: 450px;
  background-size: cover;
}

.MyPlayListPage-headerBack {
  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, .8);
}

.MyPlayListPage-inside-header {
  width: 100%;
  height: 80%;
  display: flex;
  align-items: center;
}

.MyPlayListPage-inside-header img {
  width: 230px;
  height: 240px;
  object-fit: cover;
  margin-top: 60px;
  padding-left: 35px;
}

.MyPlayListPage-inside-text {
  padding-top: 50px;
  padding-left: 30px;
}

.MyPlayListPage-inside-text h5 {
  padding-left: 3px;
}

.MyPlayListPage-inside-text .p1{
  font-size: 70px;
  font-weight: bold;
}

.MyPlayListPage-inside-text .p2{
  font-size: 14px;
  opacity: 0.6;
}

.MyPlayListPage-inside-bottom {
  display: flex;
  align-items: center;
  width: 100%;
  height: 20%;
  background-color: rgba(0, 0, 0, .8);
}

.MyPlayListPage-inside-bottom .playarrow {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  width: 55px;
  height: 55px;
  border-radius: 50px;
  font-size: 40px;
  background-color: rgb(43, 189, 43);
  margin-left: 45px;
  cursor: pointer;
}

.MyPlayListPage-inside-bottom .playarrow:hover {
  transform: scale(1.1);
}

.MyPlayListPage-inside-bottom .favorite {
  font-size: 30px;
  margin-left: 30px;
  cursor: pointer;
}

.MyPlayListPage-list {
  padding-left: 60px;
}

.MyPlayListPage-stats {
  display: flex;
  align-items: center;
  font-size: 13px;
  opacity: 0.7;
  padding-top: 20px;
  
  margin-bottom: 10px;
}

.MyPlayListPage-stats .stats-hash {
  width: 2%;
}

.MyPlayListPage-stats .stats-name {
  width: 52%;
}

.MyPlayListPage-stats .stats-album {
  width: 35%;
}

.MyPlayListPage-stats .stats-clock {
  
  font-size: 20px;
  font-weight: lighter;
}
.ProfilePage {
  position: fixed;
  right: 0;
  width: 83%;
}

.Audio {
  position: fixed;
  color: white;
  bottom: 0;
  height: 90px;
  width: 100%;
  background-color: #1f1f1f;
  display: flex;
}

.Audio-titles {
  width: 25%;
  display: flex;
  align-items: center;
}

.Audio-titles img {
  cursor: pointer;
  margin-left: 15px;
  width: 55px;
  height: 55px;
  border: 1.5px solid rgb(60, 122, 180);
  object-fit: cover;
}

.Audio-titles span {
  cursor: pointer;
  font-size: 13px;
  margin-top: 0px;
  margin-left: 15px;
}

.Audio-titles p {
  font-size: 11px;
  opacity: 0.7;
}

.Audio-main {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Audio-main span {
  font-size: 30px;
  cursor: pointer;
}

.Audio-main .play {
  font-size: 40px;
  padding: 20px;
}

.Audio-volume {
  width: 25%;
  display: flex;
  align-items: center;
}

.Audio-volume span {
  cursor: pointer;
  width: 10%;
  padding-left: 200px;
}

.volume-progress {
  cursor: pointer;
  width: 25%;
  height: 2px;
  background-color: #fff;
}
.lds-roller {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #fff;
  margin: -4px 0 0 -4px;
}
.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}
.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}
.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}
.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}
.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}
.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}
.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}
.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}
@-webkit-keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

