.MyPlayListPage {
  width: 83%;
  height: 1500px;
  margin-left: 17%;
  color: #fff;
  background-color: rgb(8, 8, 8);
}

.MyPlayListPage-header {
  height: 450px;
  background-size: cover;
}

.MyPlayListPage-headerBack {
  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, .8);
}

.MyPlayListPage-inside-header {
  width: 100%;
  height: 80%;
  display: flex;
  align-items: center;
}

.MyPlayListPage-inside-header img {
  width: 230px;
  height: 240px;
  object-fit: cover;
  margin-top: 60px;
  padding-left: 35px;
}

.MyPlayListPage-inside-text {
  padding-top: 50px;
  padding-left: 30px;
}

.MyPlayListPage-inside-text h5 {
  padding-left: 3px;
}

.MyPlayListPage-inside-text .p1{
  font-size: 70px;
  font-weight: bold;
}

.MyPlayListPage-inside-text .p2{
  font-size: 14px;
  opacity: 0.6;
}

.MyPlayListPage-inside-bottom {
  display: flex;
  align-items: center;
  width: 100%;
  height: 20%;
  background-color: rgba(0, 0, 0, .8);
}

.MyPlayListPage-inside-bottom .playarrow {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  width: 55px;
  height: 55px;
  border-radius: 50px;
  font-size: 40px;
  background-color: rgb(43, 189, 43);
  margin-left: 45px;
  cursor: pointer;
}

.MyPlayListPage-inside-bottom .playarrow:hover {
  transform: scale(1.1);
}

.MyPlayListPage-inside-bottom .favorite {
  font-size: 30px;
  margin-left: 30px;
  cursor: pointer;
}

.MyPlayListPage-list {
  padding-left: 60px;
}

.MyPlayListPage-stats {
  display: flex;
  align-items: center;
  font-size: 13px;
  opacity: 0.7;
  padding-top: 20px;
  
  margin-bottom: 10px;
}

.MyPlayListPage-stats .stats-hash {
  width: 2%;
}

.MyPlayListPage-stats .stats-name {
  width: 52%;
}

.MyPlayListPage-stats .stats-album {
  width: 35%;
}

.MyPlayListPage-stats .stats-clock {
  
  font-size: 20px;
  font-weight: lighter;
}