.Navbar {
  position: fixed;
  width: 17%;
  height: 100vh;
  background-color: rgb(0, 0, 0);
  color: #fff;
}

.Navbar-logo {
  margin-top: -34px;
  padding-left: 21px;
}

.Navbar-logo img {
  width: 134px;
}

.Navbar-list {
  margin-top: -15px;
}

.Navbar-list a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
}

.Navbar-list a.active li {
  color: #fff;
}

.Navbar-list ul li {
  padding: 5px 0px;
  display: flex;
  align-items: center;
  margin: auto;
  width: 95%;
  border-radius: 5px;
}

.Navbar-span-icon {
  padding-left: 18px;
  font-size: 28px;
}

.Navbar-span-text {
  padding-left: 15px;
  padding-top: -1px;
}

.Navbar-playlist {
  padding: 27px 25px;
}

.Navbar-playlist div {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  cursor: pointer;
}

.Navbar-playlist div:hover span {
  color: #fff;
}

.Navbar-playlist span {
  font-size: 14px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.7);
  margin-top: -4px;
  padding-left: 17px;
}

.Navbar-playlist h5 {
  opacity: .7;
  font-weight: 500;
  letter-spacing: 1px;
  margin-bottom: 17px;
  cursor: default;
}

.add-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  width: 32px;
  height: 32px;
  background-color: rgba(255, 255, 255, 0.7);
}

.favorite-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  width: 32px;
  height: 32px;
  background: linear-gradient(120deg, rgb(89, 89, 197), rgb(87, 72, 173));
  color: rgba(255, 255, 255, 0.7);
  font-size: 16px;
}

.navbar-favorite-list ul {
  padding-top: 10px;
  list-style: none;
  font-size: 15px;
  line-height: 30px;
  opacity: 0.8;
}