.Audio {
  position: fixed;
  color: white;
  bottom: 0;
  height: 90px;
  width: 100%;
  background-color: #1f1f1f;
  display: flex;
}

.Audio-titles {
  width: 25%;
  display: flex;
  align-items: center;
}

.Audio-titles img {
  cursor: pointer;
  margin-left: 15px;
  width: 55px;
  height: 55px;
  border: 1.5px solid rgb(60, 122, 180);
  object-fit: cover;
}

.Audio-titles span {
  cursor: pointer;
  font-size: 13px;
  margin-top: 0px;
  margin-left: 15px;
}

.Audio-titles p {
  font-size: 11px;
  opacity: 0.7;
}

.Audio-main {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Audio-main span {
  font-size: 30px;
  cursor: pointer;
}

.Audio-main .play {
  font-size: 40px;
  padding: 20px;
}

.Audio-volume {
  width: 25%;
  display: flex;
  align-items: center;
}

.Audio-volume span {
  cursor: pointer;
  width: 10%;
  padding-left: 200px;
}

.volume-progress {
  cursor: pointer;
  width: 25%;
  height: 2px;
  background-color: #fff;
}